import { BrowserModule } from '@angular/platform-browser';
import {LOCALE_ID, NgModule} from '@angular/core';

import { AppComponent } from './app.component';
import { IndexComponent } from './index/index.component';
import {routing} from "./app.router";
import {HttpService} from "./service/http.service";
import {FormsModule} from "@angular/forms";
import {HttpModule} from "@angular/http";
import {MenuService} from "./service/menu.service";
import {SiteService} from "./service/site.service";
import {PropertyService} from "./service/property.service";
import { MenuComponent } from './site/menu/menu.component';
import { PropertyComponent } from './modulos/property/property.component';
import { AddComponent } from './modulos/property/add/add.component';
import { EditComponent } from './modulos/property/edit/edit.component';
import { ListComponent } from './modulos/property/list/list.component';
import { ExtraComponent } from './extra/extra.component';
import { TypesComponent } from './types/types.component';
import { UsersComponent } from './users/users.component';
import {UserService} from "./service/user.service";
import { RealComponent } from './real/real.component';

@NgModule({
  declarations: [
    AppComponent,
    IndexComponent,
    MenuComponent,
    PropertyComponent,
    AddComponent,
    EditComponent,
    ListComponent,
    ExtraComponent,
    TypesComponent,
    UsersComponent,
    RealComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    routing
  ],
  providers: [

    HttpService,
    MenuService,
    SiteService,
    PropertyService,
    UserService,
    {provide: LOCALE_ID, useValue: 'pt-BR'}


  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



