import { Injectable } from '@angular/core';
import {MenuService} from "./menu.service";
import {Menu} from "../models/menu";
import {Title} from "@angular/platform-browser";

@Injectable()
export class SiteService {
  menu: Menu[];
  phones: any[];
  social: any;
  address: string;
  email: string;
  nameEnterprise: string;
  logoTop: string;
  logoFooter: string;


  constructor(private menuService: MenuService,private titleService: Title) {
    this.menu = this.menuService.menu;
    this.phones = [
      {'phone':'(34)99999-9999'},
      {'phone':'(34)77777-8888'}
    ];
    this.address = 'Rua Buenos Aires,132 - Novo Silvestre - Viçosa - MG';
    this.email = 'm2@m2imobiliaria.com.br';
    this.social = {
      'fb' : 'm2',
      'instagram' : 'm2',
      'twitter' : 'm2'
    };
    this.nameEnterprise = "M2 Imobiliária";
    this.logoTop = "../assets/images/logo2.png";
    this.logoFooter = "../assets/images/logo.png";
  }
  public setTitle(newTitle: string){
    this.titleService.setTitle(newTitle);

  }

}



