import { Component, OnInit } from '@angular/core';
import {PropertyService} from "../service/property.service";
import {TypeReal} from "../models/type_real";

@Component({
  selector: 'app-types',
  templateUrl: './types.component.html',
  styleUrls: ['./types.component.css']
})
export class TypesComponent implements OnInit {
  types: TypeReal[];
  type: TypeReal;

  constructor(private propertyService:PropertyService) { }

  ngOnInit() {
    this.propertyService.getTypes().then(r=>{
      this.types = r;
    });
    this.type = new TypeReal();
  }
  reset(){
    this.ngOnInit();
  }
  save(){
    this.propertyService.saveType(this.type).then(r=>{
      this.reset();
    });
  }
  edit(id,descricao){
    this.type.descricao = descricao;
    this.type.id = id;
  }
  delete(id){
    if(confirm('Você realmente deseja remover este item?')){
      this.propertyService.deleteType(id).then(r=>{
        this.ngOnInit();
      })
    }
  }

}



