import { Component, OnInit } from '@angular/core';
import {ExtraData} from "../models/extra_data";
import {TypeReal} from "../models/type_real";
import {PropertyService} from "../service/property.service";

@Component({
  selector: 'app-extra',
  templateUrl: './extra.component.html',
  styleUrls: ['./extra.component.css']
})
export class ExtraComponent implements OnInit {
  extra: ExtraData;
  extras: any[];
  types: TypeReal[];
  constructor(private propertyService: PropertyService) {

  }

  ngOnInit() {
      const self = this;
      this.extras = [];
      this.propertyService.getTypes().then(r=>{
          this.types = r;
          this.types.forEach(function (v) {
              self.propertyService.getExtraDatasByType(v.id).then(r=>{
                  r.forEach(function (valueExtra) {
                      self.extras.push({'extra':valueExtra,'typeReal':v})
                  })
              });


          })
      });
      this.extra = new ExtraData();
      console.log(self.extras);

  }
  getByType(idType: number){

  }
  save(){
      this.propertyService.saveExtra(this.extra).then(r=>{
          this.ngOnInit();
      });
      console.log(this.extra);
  }
  edit(id,descricao,type){
      this.extra.descricao = descricao;
      this.extra.id = id;
      this.extra.typeReal = type;
  }
  delete(id){
      if(confirm('Você realmente deseja remover este item?')){
          this.propertyService.deleteExtra(id).then(r=>{
              this.ngOnInit();
          })
      }
  }
  reset(){
      this.ngOnInit();
  }

}



