import {Routes, RouterModule, Route} from '@angular/router';
import {ModuleWithProviders} from "@angular/core";
import {IndexComponent} from "./index/index.component";
import {PropertyComponent} from "./modulos/property/property.component";
import {AddComponent} from "./modulos/property/add/add.component";
import {ExtraComponent} from "./extra/extra.component";
import {TypesComponent} from "./types/types.component";
import {UsersComponent} from "./users/users.component";
import {RealComponent} from "./real/real.component";

const APP_ROUTES:Route[] = [
  {path:'',component:IndexComponent},
  {path:'property', component:RealComponent},
  {path: 'property/add', component: AddComponent},
  {path: 'extra',component: ExtraComponent},
  {path: 'type',component: TypesComponent},
  {path: 'user',component: UsersComponent},

];

export const routing: ModuleWithProviders = RouterModule.forRoot(APP_ROUTES);



