import { Component, OnInit } from '@angular/core';
import {UserService} from "../service/user.service";

@Component({
    selector: 'app-users',
    templateUrl: './users.component.html',
    styleUrls: ['./users.component.css']
})
export class UsersComponent implements OnInit {
    user: any;
    users:any[];
    phone: any={};

    constructor(private userService:UserService) {

    }

    ngOnInit() {
        this.userService.getAll().then(r=>{
            this.users = r;
        });
        this.user = {};
    }
    edit(id){
        this.userService.get(id).then(r=>{
            this.user = r;
        })
    }
    save(){
        this.userService.save(this.user).then(r=>{
            this.ngOnInit();
        });
    }
    delete(id){
        if(confirm('Você realmente deseja remover este item?')){
            this.userService.delete(id).then(r=>{
                this.ngOnInit();
            })
        }
    }
    reset(){
        this.ngOnInit();
    }
    addPhone(){
        if(!this.user.phones){
            this.user.phones = [];
        }
        this.user.phones.push(this.phone);
        this.phone = {};
    }
    removePhone(phone: any){
        if(confirm('Você realmente deseja remover este telefone?')){
            this.user.phones = this.user.phones.filter(function (value) {
                return value != phone;
            })
        }
    }

}



