import { Injectable } from '@angular/core';
import {HttpService} from "./http.service";
import {Property, PropertyComponentInterface} from "../models/property";
import {ExtraData} from "../models/extra_data";
import {TypeReal} from "../models/type_real";
import deleteProperty = Reflect.deleteProperty;
import {RequestOptions} from "@angular/http";

@Injectable()
export class PropertyService {
  entity: String;
  properties: Property[];
  property:Property;
  flatMap: Function;
  constructor(private _httpservice: HttpService) {
    this.entity = 'property';
    this.flatMap = function(array, callback) {
      return [].concat.apply([], array.map(callback));
    }

  }


  __getAll() {
    return this._httpservice.get(this.entity);
  }
  getAll(ref:PropertyComponentInterface) {
    this.__getAll().then(r=>{
      ref.imoveis = r;
    });

  }

  __get(id: number){
    return this._httpservice.get(this.entity,id.toString());
  }
  get(ref: PropertyComponentInterface,id: number){
    return this.__get(id).then(r=>{
      ref.imovel = r;
    });
  }
  getFromWhere(ref: PropertyComponentInterface,type:string,way:string,city:string){
    var queryGet: string = '?adStatus=ACTIVE';
    if(type != 'All' && type != ''){
      queryGet +=  '&type='+type.split('_')[1];
    }
    /*if(way != 'All' && type != ''){
      queryGet +=  '&wayToNegotiete='+type.split('_')[1];
    }*/
    if(city != 'All' && city != ''){
      queryGet +=  '&city='+city;
    }

    return this._httpservice.get(this.entity,queryGet+'&o=1').then(r=>{
      ref.imoveis = r;
    });
  }
  getPropertiesByType(idType: number){
    return this._httpservice.get(this.entity,'',"idType="+ idType);

  }
  getOwner(id: number){
    return this._httpservice.get(this.entity,id.toString()+'/owner');
  }
  getType(idReal: number){
    return this._httpservice.get(this.entity,idReal.toString()+"/type");

  }
  getTypes(){
    return this._httpservice.get('type');

  }
  getCities(){
    return this._httpservice.get(this.entity,"cities");
  }

  getExtraDatasByType(idType: number) {

    return this._httpservice.get('type',idType.toString()+'/extra-data-real-estate').then((r)=>{
      return r;
    });
  }
  save(property: Property,idTypeRealEstate: number,idProprietary: number){
    deleteProperty(property,'user');
    if(property.id>0){
      let par = JSON.stringify(property);
      return this._httpservice.post(this.entity+'?idTypeRealEstate='+idTypeRealEstate+'&idProprietary='+idProprietary,par).catch(r=>{
        alert(r);
      })
    }
    deleteProperty(property,'id');
    let par = JSON.stringify(property);
    return this._httpservice.post(this.entity+'?idTypeRealEstate='+idTypeRealEstate+'&idProprietary='+idProprietary,par).catch(r=>{
      alert(r);
    })

  }
  saveExtra(extra: ExtraData){
    if(extra.id>0){
      return this._httpservice.update('extra-data-real-estate?idTypeRealEstate='+extra.typeReal,'{"id":'+extra.id+',"descricao":"'+extra.descricao+'"}').catch(r=>{
        alert(r.descricao);
      })
    }
    return this._httpservice.post('extra-data-real-estate?idTypeRealEstate='+extra.typeReal,'{"descricao":"'+extra.descricao+'"}').catch(r=>{
      alert(r.descricao);
    })

  }
  saveType(type: TypeReal){
    if(type.id>0){
      return this._httpservice.update('type','{"id":'+type.id+',"descricao":"'+type.descricao+'"}').catch(r=>{
        alert(r.descricao);
      })
    }
    return this._httpservice.post('type','{"descricao":"'+type.descricao+'"}').catch(r=>{
      alert(r.descricao);
    })

  }
  getExtras(){
    let self = this;
    return this._httpservice.get('type').then(r=>{
      return self.flatMap(r,function (v,k) {
        return v.extraDataRealEstates.map(function (v2,kk) {
          return {"extra":v2 ,'type':v.descricao,'type_id':v.id};
        })
      });
    })
  }
  deleteExtra(id: number){
    return this._httpservice.delete('extra-data-real-estate',id.toString());
  }
  deleteType(id: number){
    return this._httpservice.delete('type',id.toString());
  }


  delete(id: number) {
    return this._httpservice.delete(this.entity,id.toString());
  }
  upload(formData,id) {
    return this._httpservice.upload('file?idRealEstate='+id,formData);
  }
  deletePhoto(idPhoto: number,name:string,typeReal:string){
    this._httpservice.delete('photo',idPhoto.toString()).catch();
    return this._httpservice.delete('file',typeReal+'/'+name)

  }
}




