import {Component, OnInit, ViewChild} from '@angular/core';
import {Property} from "../models/property";
import {PropertyService} from "../service/property.service";
import {UserService} from "../service/user.service";
import {TypeReal} from "../models/type_real";
import {Photo} from "../models/photo";
import deleteProperty = Reflect.deleteProperty;
import {ExtraData} from "../models/extra_data";
import {ValueOfFeature} from "../models/value_of_feature";
import {isArray, isUndefined} from "util";

@Component({
  selector: 'app-real',
  templateUrl: './real.component.html',
  styleUrls: ['./real.component.css']
})
export class RealComponent implements OnInit {
  property: Property;
  properties: Property[];
  users: any[];
  types: TypeReal[];
  extraDatas: ExtraData[];
  idProprietary: number;
  idTypeRealEstate: number;
  image: any;
  @ViewChild('fileInput') fileInput;
  h:Property[] = [];

  constructor(private propertyService: PropertyService,private userService: UserService) { }

  ngOnInit() {
    this.idProprietary = 0;
    this.idTypeRealEstate = 0;
    this.property = new Property();
    this.property.adStatus = 'ACTIVE';
    this.idProprietary = 1;
    this.idTypeRealEstate = 1;
    this.property.wayNegotiateType = 'ALUGUEL';
    this.h = [];
    this.properties = [];
    this.userService.getAll().then(r=>{
      this.users = r;
    });

    this.propertyService.getTypes().then(r=>{
      this.types = r;
      const self = this;
      r.forEach(function (v,i) {
        self.propertyService.getPropertiesByType(v.id).then(r=>{
          r.forEach(function (valueReal,i2) {
            valueReal['typeReal'] = v;
            self.properties.push(valueReal);

          })
        });
      });

    });

  }
  intersect(a, b) {
    return a.filter(function(n) {
      return b.map(function(e) { return e.id; }).indexOf(n.id) !== -1;
    });
  }
  changeType(){

    this.propertyService.getExtraDatasByType(this.idTypeRealEstate).then(r=>{
      this.extraDatas = r;
      this.property.valueOfFeatures = this.extraDatas.map(function (v) {
        let nv  = new ValueOfFeature();
        nv.extraDataRealEstate = v;
        return nv;

      });
    })


  }

  edit(id,idType){
    this.property = new Property();

    this.propertyService.getOwner(id).then(r=>{
      this.idProprietary = r.id;
    });
    return this.propertyService.__get(id.toString()).then(r=>{
      if(idType!=this.idTypeRealEstate){
        this.idTypeRealEstate = idType;
        this.changeType();
      }
      this.property = r;

    });

  }
  save(){

    return this.propertyService.save(this.property,this.idTypeRealEstate,this.idProprietary).then(r=>{
      this.reset();
    })
  }
  delete(id){
    if(confirm('Você realmente deseja remover este item?')){
      this.propertyService.delete(id).then(r=>{
        this.reset();
      })
    }
  }
  reset(){
    this.ngOnInit();
  }
  upload() {
    let fileBrowser = this.fileInput.nativeElement;
    if (fileBrowser.files && fileBrowser.files[0]) {
      const formData = new FormData();
      formData.append("image", fileBrowser.files[0]);
      this.propertyService.upload(formData, '1').then(res => {
        let nPhoto: Photo = new Photo();
        console.log(res);
        nPhoto.description = res['links'][0];
        nPhoto.urlImg = res['links'][0];
        if(!this.property.photos){
          this.property.photos = []
        }
        this.property.photos.push(nPhoto);
        console.log(res);
      });
    }
  }
  splitImg(imgUrl:string){
    const folds = imgUrl.split('/');
    const tam = folds.length;
    const narr = [];
    for(let i=0;i<3;i++){
      if(!isUndefined(folds[tam-i]))
        narr.push(folds[tam-i]);
    }
    console.log(narr);
    return {name: narr[0], type: narr[1]};

  }
  deletePhoto(imId: number,imgSplit: object){
    if(confirm('Você realmente deseja remover esta imagem?')){
      const name = imgSplit['name'];
      const typeReal = imgSplit['type'];
      const find = this.property.photos.filter(function(value){
        return value.id != imId;
      });
      this.property.photos = find;
      this.propertyService.deletePhoto(imId,name,typeReal);
    }
  }

}



