import {Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {Property} from "../../../models/property";
import {PropertyService} from "../../../service/property.service";
import {TypeReal} from "../../../models/type_real";
import {ExtraData} from "../../../models/extra_data";
import {ValueOfFeature} from "../../../models/value_of_feature";
import {getDOM} from "@angular/platform-browser/src/dom/dom_adapter";

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.css']
})
export class AddComponent implements OnInit {
  imovel: Property;
  tipos: TypeReal[];
  extra: ExtraData[];
  value: ValueOfFeature[];
  @ViewChild('img') img: ElementRef;

  constructor(private propertyService: PropertyService) {
    this.propertyService.getTypes().then(r=>{
      this.tipos = r
    });
    this.imovel = new Property();
    this.imovel.description ="Casa";
    this.imovel.typeReal = new TypeReal();

  }
  onChangeType(){
    const idType=this.imovel.typeReal.id;
    this.propertyService.getExtraDatasByType(idType).then(r=>{
      this.extra = r;
    });
  }
  onFileChange(event){
    let reader = new FileReader();
    if(event.target.files && event.target.files.length > 0) {
      let file = event.target.files[0];
      reader.readAsDataURL(file);
      reader.onload = () => {
        this.img.nativeElement.setValue({
          filename: file.name,
          filetype: file.type,
          value: reader.result.split(',')[1]
        })
      };
    }
  }


  ngOnInit() {
  }

}



