import { Injectable } from '@angular/core';
import { Headers, Http, Response, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/toPromise';

@Injectable()
export class HttpService {
  urlServer = 'http://sitebreno.jelasticlw.com.br/webapi/';
  private headerHTTP = new Headers({'Content-Type': 'application/json'});
  retorno = '';

  constructor(private http: Http) { }
  get(entity: String, where: String = '',query: string='') {
    let extra = '';
    if (where === '') {
      extra = '/';

    } else {
      extra = '/' + where + '/';
    }
    if(query!==''){
      extra += '?'+query;
    }
    const ret = this.http.get(this.urlServer + entity + extra, { headers: this.headerHTTP }).
      toPromise().
      then(res => {
        this.retorno = res.json();
        return res.json();
      });

    return ret;

  }
  delete(entity: String, id: String) {
    const ret = this.http.delete(this.urlServer + entity + '/' + id, { headers: this.headerHTTP })
      .toPromise()
      .then(res => res.json());
    return ret;
  }
  update(entity: String, params: String) {
    const ret = this.http.put(this.urlServer + entity, params, { headers: this.headerHTTP })
    .toPromise()
    .then(res => res.json());
    return ret;
  }
  post(entity: String, params: String) {
    const ret = this.http.post(this.urlServer + entity, params, { headers: this.headerHTTP })
    .toPromise()
    .then(res => res.json());
    return ret;
  }
  upload(entity: String, formData:FormData){
    return new Promise((resolve, reject) => {
      let xhr = new XMLHttpRequest();
      xhr.onreadystatechange = () => {
        if (xhr.readyState === 4) {
          if (xhr.status === 201) {
            resolve(JSON.parse(xhr.response));
          } else {
            reject(xhr.response);
          }
        }
      };
      xhr.open('POST', this.urlServer + entity, true);
      xhr.withCredentials = true;
      xhr.send(formData);
    });
  }

}



