import { Injectable } from '@angular/core';
import {Menu} from "../models/menu";

@Injectable()
export class MenuService {
  menu: Menu[];

  constructor() {
    this.menu = [
      {
        'link' : '/',
        'title': 'Home',
        'description': 'Página Inicial',
        'routerLink' : false

      },
      {
        'link' : '/quemsomos',
        'title': 'Quem Somos',
        'description': 'Quem Somos',
        'routerLink' : true
      },
      {
        'link' : '/services',
        'title': 'Serviços',
        'description': 'Serviços',
        'routerLink' : true
      },
      {
        'link' : '/property',
        'title': 'Imóveis',
        'description': 'Imoveis',
        'routerLink' : true
      }
    ]

  }

}



