import {Photo} from "./photo";
import {TypeReal} from "./type_real";
import {ValueOfFeature} from "./value_of_feature";

export class Property{
  id: number;
  price: number;
  street: string;
  number: string;
  neighborhood: string;
  city: string;
  uf: string;
  description: string;
  constructArea: number;
  wayNegotiateType: string;
  adStatus: string;
  typeReal: TypeReal;
  detail: string;
  valueOfFeatures: ValueOfFeature[];
  photos: Photo[];



}
export interface PropertyComponentInterface{
  imovel:Property;
  imoveis:Property[];
}



